.App {
  width: calc(100vw - 10px);
  height: calc(100vh);

  &-main {
    width: 100%;
    height: calc(100% - 41px);
  }

  &-footer {
    width: 100%;
    height: 40px;
    background-color: #bdbdbd;
    border-top: 1px solid #4a4a4a;
    display: flex;
    justify-content: flex-end;
  }
}
